.card {
  --avatar-offset: 54px;
  --avatar-dim: 108px;
  display: flex;
  flex-direction: column;
  width: 280px;
  padding: 0 20px;
  border-radius: 5px;
  text-align: center;
  position: relative;
  margin-top: calc(var(--avatar-offset) + 20px);
  margin-right: 5px;
  margin-left: 5px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  display: grid;
  grid-auto-rows: 80px minmax(140px, auto) minmax(60px, auto) 80px;
}
.card .header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 17px;
}

.card .location i {
  margin-right: 5px;
}

.card .location p {
  margin: 0;
  margin-top: 2px;
}

.card .country {
  background: transparent;
  border: none;
  cursor: pointer;
  color: hsl(0, 0%, 29%);
  display: flex;
  flex-direction: row;
  align-items: center;
}

.card .country:hover {
  color: var(--tag-color);
}

.card .avatar {
  width: var(--avatar-dim);
  height: 118px;
  margin: calc(var(--avatar-offset) * -1) auto 0;
}

.card .avatar i {
  position: absolute;
  font-size: var(--avatar-dim);
  width: var(--avatar-dim);
  height: var(--avatar-dim);
  background: hsl(0, 0%, 100%);
  color: rgb(155, 155, 155);
  border-radius: 50%;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14),
    0 1px 10px 0 rgba(0, 0, 0, 0.12);
  display: block;
}

.card .avatar img {
  display: block;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
  width: var(--avatar-dim);
  height: var(--avatar-dim);
}

.card .avatar img.broken {
  display: none !important;
}

.card .name {
  color: hsl(0, 0%, 29%);
  font-size: 20px;
  margin: 0 0 5px;
}

.card .title {
  color: hsl(0, 0%, 29%);
  margin: 0 0 5px;
  font-weight: 400;
}

.card .description {
  font-style: italic;

  color: #16385c;
  font-weight: 600;
  margin-top: 0;
  padding: 5px 2.5px;
  border-radius: 10px;
}

.card .tags {
  z-index: 2;
}

.card .tag {
  color: hsl(0, 0%, 100%);
  background: #16385c;
  border: 0;
  border-radius: 20px;
  display: inline-block;
  padding: 5px 10px;
  margin: 0 2.5px 5px;
  line-height: 1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-transform: lowercase;
  font-family: monospace;
}

.card .tag:hover {
  cursor: pointer;
}

.card .channels {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: flex-end;
}

.card .channels .channel-inner {
  display: flex;
  justify-content: space-around;

  background-color: #d0f8ef;
  align-items: center;
  padding: 20px 10px 5px 10px;
  margin-right: -20px;
  margin-left: -20px;
}
.card .wave {
  position: absolute;
  bottom: 60px;
  left: 0;
  background-color: hsl(0, 0%, 100%);
  height: 30px;
  width: 100%;
  border-radius: 0% 0% 50% 50%;
}

.card .channels a {
  color: #16385c;
  text-decoration: none;
}

.card .channels a .icon {
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  -webkit-transform-origin: 50%;
          transform-origin: 50%;
}

.card .channels a:hover .icon {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.card .channels a .type::first-letter {
  text-transform: uppercase;
}

.card .channels a {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card .channels .type {
  margin: 5px 0px;
}

.channel-label {
  text-decoration: none;
}

.card .like-button {
  background: none;
  border: none;
  cursor: pointer;
}

.card .liked,
.card .like-button:hover i {
  color: hsl(0, 71%, 51%);
}

